.form-base{
    display: flex;
    flex-direction: column;
    width: 40%;
    border: 1px solid grey;
    padding: 20px;
    border-radius: 20px;
}
.main-div{
    display: flex;
    justify-content: center;
    text-align: center;
    margin-top: 50px;
}
.input{
    height: 25px;
    padding: 10px;
    border-radius: 10px;
    font-size: 20px;
}
.btn{
    height: 50px;
    border-radius: 25px;
    font-size: 25px;
}
.main-form{
    width: 20%;
    font-size: 50px;
    border: 1px solid grey;
    margin-left: 560px;
    border-radius: 20px;
    padding: 10px;

}
